.box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 640px;
  margin: 0 auto;
}

.heading {
  font-size: 2rem;
}

.img-heading,
.img-subheading,
.img-text
{
  width: 100%;
}

.heading {
  color: #2D3648;
  text-align: center;
  font-family: Inter, sans-serif;
  font-size: 56px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
}

.button-container {
  display: flex;
  justify-content: space-around;
}

@media screen and (max-width: 694px) {
  .box {
    max-width: 480px;
  }

  .heading {
    font-size: 1.8rem;
  }
}
