.background {
  padding: 64px 104px;
  margin: 0;
  background-color: #1A202C;
}

.list {
  display: flex;
  list-style-type: none;
  margin: 0 auto;
  padding: 0;
  gap: 40px;
  color: #FFFFFF;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1220px) {
  .list {
    gap: 20px;
  }
}

@media screen and (max-width: 1120px) {
  .background {
    padding: 64px;
  }

  .list {
    flex-direction: column;
  }
}
