.navbar {
  display: grid;
  grid-template-columns: 1fr 4fr;
  padding: 24px 104px;
  align-items: center;
}

.hamburger-container {
  display: none;
}

.hamburger-icon {
  width: 48px;
  height: 48px;
}

.navbar-container {}


.navbar__list {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar__item {
  margin-right: 32px;
}

.navbar__link {
  position: relative;
  display: block;
  padding: 8px 0;
  margin: 0 auto;
  text-decoration: none;
  color: #2D3648;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.01em;
  text-align: center;
}

.navbar__link::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #2D3648;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
  transform: scaleX(0);
}

.navbar__link:hover::after {
  transform: scaleX(1);
}

@media (max-width: 1366px) {
  .navbar {
    display: flex;
    justify-content: flex-end;
    padding: 24px;
  }

  .hamburger-container {
    display: block;
    position: absolute;
    top: 40px;
    right: 40px;
    z-index: 40;
  }

  .hamburger-icon.active {
    filter: brightness(0) saturate(100%) invert(100%) sepia(53%) saturate(2%) hue-rotate(98deg) brightness(108%) contrast(100%);
  }

  .navbar-container {
    position: absolute;
    right: 0;
    top: 0;
    background-color: #2D3648;
    width: 0;
    height: 100vh;
    transition: all 0.3s ease-in;
    overflow: hidden;
    z-index: 30;
  }

  .navbar-container.active {
    width: 100%;
  }

  .navbar__item {
    margin: 0 auto;
  }

  .navbar__link {
    color: #FFFFFF;
  }

  .navbar__list {
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }

}


